import React from 'react';
import PropTypes from 'prop-types';
import { NavLink, withRouter, Link } from 'react-router-dom';


class Header extends React.Component {

  constructor(props) {
    super(props);
  }

  handleClose() {
    const { closeFunction } = this.props

    closeFunction()
  }

  render() {
    const { isWhiteBg, isMod, hasLinks } = this.props
    console.log(this.props)

    const bioLinks = hasLinks ? (
      <div className="l-menu__top-right">
        <a href="https://github.com/kozlowskam" target="_blank" rel="noopener noreferrer"><img className="l-menu__top-right-logo" src={require('../static/images/github_logo.png')} alt="github logo" /></a>
        <a href="https://www.linkedin.com/in/malgorzatta-kozlowska/" target="_blank" rel="noopener noreferrer"><img className="l-menu__top-right-logo" src={require('../static/images/linkedin_logo.png')} alt="linkedin logo" /></a>
      </div>
    ) : null

    const whiteBgClass = isWhiteBg ? ' is-white-bg' : '';
    const headerImg = isWhiteBg ? (
      <img className="l-header__logo" src={require('../static/images/arrow_back.png')} alt="back to menu" />
    ) : (
        <img className="l-header__logo" src={require('../static/images/arrow_back-white.png')} alt="back to menu" />
      );

    const closeButton = isMod ? (
      isWhiteBg ? (
        <div className="l-header__close" onClick={() => this.handleClose()}>
          <img className="l-header__logo" src={require('../static/images/close.png')} alt="close" />
        </div>
      ) : (
          <div className="l-header__close" onClick={() => this.handleClose()}>
            <img className="l-header__logo" src={require('../static/images/close-white.png')} alt="close" />
          </div>
        )
    ) : null

    return (
      <div className={`l-header${whiteBgClass}`}>
        <div className="l-header__wrap">
          <nav className="l-header__menu">
            <Link to="/" className="l-header__left">{headerImg}<div className="l-header__back-text">BACK TO MENU</div></Link>
            {closeButton}
            {bioLinks}
          </nav>
        </div>
      </div>
    )
  }
}

export default Header